import axios from 'axios';
const server = process.env.REACT_APP_API_URL;
export const postTeamHandler = async (
	recruitIdx: number,
	userIdx: number,
	paymentDay: number
) => {
	try {
		const { data } = await axios.post(server + `/team`, {
			recruitIdx,
			userIdx,
			paymentDay,
		});
		return data;
	} catch (err) {
		console.log(err);
	}
};

export const delTeamHandler = async (teamIdx: number) => {
	try {
		const { data } = await axios.delete(server + `/team/${teamIdx}`);
		return data;
	} catch (err) {
		console.log(err);
	}
};

export const getMemberHandler = async (teamIdx: number, userIdx: number) => {
	try {
		const { data } = await axios.get(
			server + `/team/${teamIdx}/evaluation/${userIdx}`
		);
		return data;
	} catch (err) {
		console.log(err);
	}
};

export const evalueTeamHandler = async (
	teamIdx: number,
	userIdx: number,
	reliability: number[]
) => {
	try {
		const { data } = await axios.post(server + `/team/${teamIdx}/evaluation`, {
			userIdx,
			reliability,
		});
		return data;
	} catch (err) {
		console.log(err);
	}
};
