import axios from 'axios';
import { addPostObj } from '../../pages/AddOtt';
const server = process.env.REACT_APP_API_URL;
export const postMyOttHandler = async (
	userIdx: number,
	postObj: addPostObj,
	paymentDay: number
) => {
	try {
		const { data } = await axios.post(server + `/user/ott`, {
			...postObj,
			platformIdx: postObj.platformIdx + 1,
			userIdx,
			paymentDay,
		});
		return data;
	} catch (err) {
		console.log(err);
	}
};

export const getUserOttHandler = async (userIdx: number) => {
	try {
		const { data } = await axios.get(server + `/user/${userIdx}/ott`);
		return data;
	} catch (err) {
		console.log(err);
	}
};
